.fix {
    position: relative;
    top: -7px;
}

.market {
    color: black;
}
.sweet-loading{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50% -50%);
        -ms-transform: translate(-50% -50%);
            transform: translate(-50% -50%);
    -webkit-transform: scale(4);
        -ms-transform: scale(4);
            transform: scale(4);
}


